import React, {useEffect, useState} from 'react'
import styles from './StickyNavbar.module.scss'
import debounce from 'lodash.debounce'
import { Link } from 'react-scroll'
import { CSSTransition } from 'react-transition-group'
import { useCallback } from 'react'

const StickyNavbar = () => {
    const [prevScrollPos, setPrevScrollPos] = useState(0);
    const [visible, setVisible] = useState();
    const time = useCallback(() => prevScrollPos > 800 || prevScrollPos < 500 ? 100 : 10, [prevScrollPos])

    const handleScroll = debounce(() => {
        const currentScrollPos = window.pageYOffset;
        setPrevScrollPos(currentScrollPos);
        const isVisible = document.querySelector(".navbar_menu")
            .getBoundingClientRect().top < -150;
        if (visible !== isVisible) {
            setVisible(isVisible)
        }
    }, time);

    useEffect(() => {
        window.addEventListener("scroll", handleScroll);
    
        return () => window.removeEventListener("scroll", handleScroll);
    }, []);

  return (
    <CSSTransition in={visible} timeout={300} unmountOnExit classNames="alert">
       <div className={styles.root}>
            <ul className={styles.container}>
                <Link className={styles.link} offset={-150} activeClass="active" smooth={false} spy to="pizza">
                    Pizza</Link>
                <Link className={styles.link} offset={-45} activeClass="active" smooth={false} spy to="">
                    Snacks</Link>
                <Link className={styles.link} offset={-50} activeClass="active" smooth={false} spy to="">
                    Drinks</Link>
                <Link className={styles.link} offset={-45} activeClass="active" smooth={false} spy to="">
                    Sauces</Link>
                <Link className={styles.link} offset={-45} activeClass="active" smooth={false} spy to="">
                    Desserts</Link>
                <Link className={styles.link} offset={-45} activeClass="active" smooth={false} spy to="">
                    Hot</Link>
                <Link className={styles.link} offset={-45} activeClass="active" smooth={false} spy to="">
                    Combo</Link>
            </ul>
        </div>
    </CSSTransition>
    )
}

export default StickyNavbar;
